@media (max-width: 830px) {
  header {
    top: 0;
    .mailbox {
      display: none;
    }
    .header-box {
      .visible {
        width: 260px;
      }
    }

    .header-container {
      .header-box {
        #menu-btn {
          display: none;
        }
        #menu-btn-mobile {
          height: 20px;
          width: 30px;
          display: block;

          #hamburger {
            height: 100%;
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            justify-content: space-between;
            .top-bread,
            .meat,
            .bottom-bread {
              transition: all 0.3s;
            }
          }

          .white {
            .top-bread,
            .meat,
            .bottom-bread {
              width: 100%;
              height: 2px;
              background-color: white;
            }
          }

          .black {
            .top-bread,
            .bottom-bread {
              width: 100%;
              height: 2px;
              background-color: black;
            }
            .top-bread {
              transform: translate(0, 9px) rotate(-45deg);
            }
            .bottom-bread {
              transform: translate(0, -9px) rotate(45deg);
            }
          }
        }

        .black-text {
          #hamburger {
            .top-bread,
            .meat,
            .bottom-bread {
              background-color: black;
            }
          }
        }
      }

      .header-background {
        #menu-btn-mobile {
          #hamburger {
            .top-bread,
            .meat,
            .bottom-bread {
              background-color: black;
            }
          }
        }
      }
    }
  }

