@import "../Variables/variables";

.animations {
  grid-template-columns: repeat(3, 398px);
  grid-auto-rows: auto;
  grid-template-areas:
    "box1 box1 box1"
    "box2 box2 box2"
    "box3 box3 box3"
    "box4 box4 box4"
    "box5 box5 box5"
    "box6 box6 box6"
    "box7 box7 box7";

  .project-view-tile {
    opacity: 0;
    transition: all 0.5s;
    .animation {
      width: 100%;
      height: 100%;
    }

    a {
      color: #fff;
      font-size: $font-size-regular;
    }
  }

  #view-tile-1 {
    height: 655px;
    grid-area: box1;
  }

  #view-tile-2 {
    min-height: 100px;
    grid-area: box2;
    background-color: transparent;
    font-size: $font-size-medium-subpages;
    div p:first-child,
    span {
      font-weight: $font-weight-bold;
    }
  }
  #view-tile-3 {
    height: 655px;
    grid-area: box3;
  }
  #view-tile-4 {
    height: 655px;
    grid-area: box4;
  }
  #view-tile-5 {
    height: 690px;
    grid-area: box5;
  }
  #view-tile-6 {
    height: 690px;
    grid-area: box6;
  }
  #view-tile-7 {
    height: 690px;
    grid-area: box7;
  }

  .tile-visible {
    opacity: 1;
  }
}
